@Component({
  selector: 'app-chatroom',
  templateUrl: './chatroom.component.html',
  styleUrls: ['./chatroom.component.less']
})
export class ChatroomComponent implements OnInit {

  loginName: string = cookie.get('userName');

  content: string = '';
  bubbles: BubbleData[] = [];
  users: User[];

  constructor(private yunxinService: YunXinService) {
    yunxinService.missionUser$.subscribe(users => this.users = users);
    yunxinService.missionBubble$.subscribe((message: Message) => {
      let bubble: BubbleData = new BubbleData(message.sender, message.data, false);

      if (message.sender === cookie.get('userName')) bubble.own = true;

      this.bubbles.push(bubble);

    });
    
  }

  ngOnInit() {
    this.yunxinService.init();

    // this.ws = new WebSocket('ws://127.0.0.1:3000/ws/link/1');
    // this.ws.onmessage = msg => console.log(msg);
  }

  keyHandler(event: KeyboardEvent) {
    // 非enter键 直接返回
    if (event.keyCode !== 13) { return; }
    if (event.ctrlKey) {
      // ctrl + enter组合键，换行
      this.content += '\n';
    } else {
      // enter键，发送
      event.preventDefault();
      // console.log('发送信息: ' + this.content);
      this.yunxinService.sendMessage(this.content);
      // this.bubbles.push({own: true, name: 'xylin', time: '2-14 19:40', content: this.content});
      this.content = '';
    }
  }

}



