export const cookie = {
  set(name: string, value: any, options: any = {}) {
    
    let str = this.encode(name) + '=' + this.encode(value);

    if (null == value) options.maxage = -1;

    if (options.maxage) {
      options.expires = new Date(+new Date + options.maxage);
    }

    str += '; path=/';
    if (options.domain) str += '; domain=' + options.domain;
    if (options.expires) str += '; expires=' + options.expires.toUTCString();
    if (options.secure) str += '; secure';

    document.cookie = str;
  },
  get(name: string) {
    return this.all()[name];
  },
  del(name: string) {
    let exp = new Date();
    exp.setTime(exp.getTime() - 1);
    const cval = this.get(name);

    if (cval) {
      document.cookie = this.encode(name) + "=" + cval + "; expires=" + exp.toUTCString() + '; path=/';
    }
  },
  all() {
    let str;
    try {
      str = document.cookie;
    } catch (err) {
      if (typeof console !== 'undefined' && typeof console.error === 'function') {
        console.error(err.stack || err);
      }
      return {};
    }
    return this.parse(str);
  },
  parse(str: string) {
    const obj = {};
    const pairs = str.split(/ *; */);
    let pair;
    if ('' == pairs[0]) return obj;
    for (let i = 0; i < pairs.length; ++i) {
      pair = pairs[i].split('=');
      obj[this.decode(pair[0])] = this.decode(pair[1]);
    }
    return obj;
  },
  encode(value: any) {
    try {
      return encodeURIComponent(value);
    } catch (e) {
      console.error('error `encode(%o)` - %o', value, e)
    }
  },
  decode(value: any) {
    try {
      return decodeURIComponent(value);
    } catch (e) {
      console.error('error `decode(%o)` - %o', value, e)
    }
  }
}



