import { Injectable } from '@angular/core';
import { Subject }    from 'rxjs/Subject';

import { cookie } from './utils/cookie';

import { Message } from './Message';
import { User } from './User';


@Injectable()
export class YunXinService {
  ws: WebSocket;
  token: string;

  private missionUserSource = new Subject<User[]>();
  private missionBubbleSource = new Subject<Message>();

  missionUser$ = this.missionUserSource.asObservable();
  missionBubble$ = this.missionBubbleSource.asObservable();

  init(): void {
    this.token = cookie.get('userName');
    this.ws = new WebSocket('ws://127.0.0.1:3000/ws/link/' + this.token);
    this.ws.onmessage = msg => { this.handlerMessage(msg) };
  }

  sendMessage(msg: string): void {
    let message = new Message('message', msg, this.token);
    this.ws.send(JSON.stringify(message));
  }

  handlerMessage(msg: any): void {
    let message = JSON.parse(msg.data);

    if (message.type === 'userlist') {
      this.missionUserSource.next(message.data);
    } else if (message.type === 'message') {
      this.missionBubbleSource.next(message);
    }
  }

}


